// HeroCarousal.tsx
// HeroCarousal.tsx
'use client';

import { useState, useEffect } from 'react';
import Image from 'next/image';
import { Facebook, Instagram, Twitter } from 'lucide-react';

const slides = [
  {
    id: 1,
    background: '/images/bg 1.jpg',
    foreground: '/images/fg 1.png',
    leftContent: {
      title: 'Aquarium Installation and Maintenance',
      subtitle: 'Fastest and easiest way to cycle your new aquarium.',
      buttonText: 'CONTACT US TODAY!',
    },
    rightContent: [
      {
        type: 'specialists',
        title: 'Looking for aquarium specialists?',
        description: 'We have built our reputation on our obsessive attention to detail...',
        image: '/images/fg 1.png',
        buttonText: 'READ MORE',
      },
      {
        type: 'ideas',
        title: 'Different aquariums for different ideas',
        description: 'If you are interested in a bespoke aquarium, no matter the size...',
        buttonText: 'CONTACT DETAILS',
      },
      { type: 'video' },
    ],
  },
  {
    id: 2,
    background: '/images/bg 2.jpg',
    foreground: '/images/fg 2.png',
    leftContent: {
      title: 'Custom Bespoke Aquascaping',
      subtitle: 'Bringing the beauty of nature into your home or office.',
      buttonText: 'EXPLORE DESIGNS',
    },
    rightContent: [
      {
        type: 'specialists',
        title: 'Freshwater & Saltwater Systems',
        description: 'Expertise in both vibrant freshwater and complex marine ecosystems.',
        image: '/images/fg 2.png',
        buttonText: 'LEARN MORE',
      },
      {
        type: 'ideas',
        title: 'Scheduled Maintenance Plans',
        description: 'Keep your aquarium pristine with our reliable service plans.',
        buttonText: 'VIEW PLANS',
      },
      { type: 'video' },
    ],
  },
];

const Carousel = () => {
  const [currentSlide, setCurrentSlide] = useState(0);
  const [isAnimatingOut, setIsAnimatingOut] = useState(false);
  const [hasLoaded, setHasLoaded] = useState(false);
  const [showContent, setShowContent] = useState(false);

  useEffect(() => {
    setHasLoaded(true);

    // show first content only after initial image transition
    const firstTimer = setTimeout(() => {
      setShowContent(true);
    }, 1500);

    const interval = setInterval(() => {
      setIsAnimatingOut(true);
      setShowContent(false); // hide content during slide transition
      setTimeout(() => {
        setCurrentSlide((prev) =>
          prev === slides.length - 1 ? 0 : prev + 1
        );
        setIsAnimatingOut(false);

        // wait for bg/fg transition to finish, then show content
        setTimeout(() => {
          setShowContent(true);
        }, 1500);
      }, 1500);
    }, 9000);

    return () => {
      clearTimeout(firstTimer);
      clearInterval(interval);
    };
  }, []);

  const slide = slides[currentSlide];

  const getRightContentAnimationClass = (isExiting: boolean) => {
    if (!showContent) return 'opacity-0';
    return isExiting ? 'animate-fadeOut' : 'opacity-0 animate-fadeInUp';
  };

  const getLeftContentAnimationClass = (isExiting: boolean) => {
    if (!showContent) return 'opacity-0';
    return isExiting ? 'animate-fadeOut' : 'opacity-0 animate-fadeInLeft';
  };

  const baseDelay = 0.2;

  // --- UPDATED: Logic reversed to make slide move top â†’ bottom ---
  const getImagePositionClass = (index: number) => {
    if (index === currentSlide) {
      return 'translate-y-0';
    }
    if (
      hasLoaded &&
      index === (currentSlide - 1 + slides.length) % slides.length
    ) {
      return 'translate-y-full'; // outgoing slides move DOWN
    }
    return '-translate-y-full'; // upcoming slides wait ABOVE
  };

  return (
    <div className="relative w-full h-screen overflow-hidden bg-gray-900">
      {/* Background and Foreground Images */}
      {slides.map((s, index) => (
        <div
          key={`bg-${s.id}`}
          className={`absolute inset-0 transition-all duration-[1500ms] ease-in-out ${getImagePositionClass(
            index
          )} ${index === currentSlide ? 'opacity-100' : 'opacity-0'}`}
        >
          <div
            className={`h-full w-full ${
              index === currentSlide ? 'animate-slowZoom' : ''
            }`}
            style={{ animationFillMode: 'forwards' }}
          >
            <Image src={s.background} alt="" fill className="object-cover" />
            <div className="absolute inset-0 bg-black/50"></div>
          </div>
        </div>
      ))}
      {slides.map((s, index) => (
        <div
          key={`fg-${s.id}`}
          className={`absolute inset-0 transition-all duration-[1500ms] ease-in-out ${getImagePositionClass(
            index
          )} ${index === currentSlide ? 'opacity-100' : 'opacity-0'}`}
        >
          <Image
            src={s.foreground}
            alt=""
            fill
            className="object-contain object-center"
          />
        </div>
      ))}

      {/* Content Sections */}
      <div className="absolute inset-0 p-8 md:p-16 lg:p-24 flex flex-col justify-between z-10">
        <div className="text-white">
          <h1
            className={`text-5xl md:text-7xl font-bold leading-tight ${getLeftContentAnimationClass(
              isAnimatingOut
            )}`}
            style={{ animationDelay: `${baseDelay}s` }}
          >
            {slide.leftContent.title.split('\n').map((line, i) => (
              <span key={i} className="block">
                {line}
              </span>
            ))}
          </h1>
          <p
            className={`mt-4 text-lg md:text-xl max-w-md ${getLeftContentAnimationClass(
              isAnimatingOut
            )}`}
            style={{ animationDelay: `${baseDelay + 0.2}s` }}
          >
            {slide.leftContent.subtitle}
          </p>
          <button
            className={`mt-8 px-8 py-3 bg-teal-500 text-white font-semibold rounded hover:bg-teal-600 transition-colors ${getLeftContentAnimationClass(
              isAnimatingOut
            )}`}
            style={{ animationDelay: `${baseDelay + 0.4}s` }}
          >
            {slide.leftContent.buttonText}
          </button>
        </div>
        <div className="flex space-x-4">
          <div
            className={`w-10 h-10 border border-white/50 flex items-center justify-center text-white hover:bg-white/10 ${getLeftContentAnimationClass(
              isAnimatingOut
            )}`}
            style={{ animationDelay: `${baseDelay + 0.6}s` }}
          >
            <Twitter size={20} />
          </div>
          <div
            className={`w-10 h-10 border border-white/50 flex items-center justify-center text-white hover:bg-white/10 ${getLeftContentAnimationClass(
              isAnimatingOut
            )}`}
            style={{ animationDelay: `${baseDelay + 0.7}s` }}
          >
            <Facebook size={20} />
          </div>
          <div
            className={`w-10 h-10 border border-white/50 flex items-center justify-center text-white hover:bg-white/10 ${getLeftContentAnimationClass(
              isAnimatingOut
            )}`}
            style={{ animationDelay: `${baseDelay + 0.8}s` }}
          >
            <Instagram size={20} />
          </div>
        </div>
      </div>
      <div className="absolute top-1/2 right-0 -translate-y-1/2 w-full lg:w-1/2 max-w-2xl p-8 md:p-16 lg:p-24 z-10">
        <div className="space-y-4">
          <div
            className={`bg-white p-6 rounded-lg shadow-2xl flex items-center space-x-4 ${getRightContentAnimationClass(
              isAnimatingOut
            )}`}
            style={{ animationDelay: `${baseDelay + 0.3}s` }}
          >
            <div
              className={`w-1/3 ${getRightContentAnimationClass(
                isAnimatingOut
              )}`}
              style={{ animationDelay: `${baseDelay + 0.4}s` }}
            >
              <Image
                src={slide.rightContent[0].image as string}
                alt=""
                width={150}
                height={100}
                className="object-contain"
              />
            </div>
            <div className="w-2/3">
              <h3
                className={`font-bold text-xl mb-2 ${getRightContentAnimationClass(
                  isAnimatingOut
                )}`}
                style={{ animationDelay: `${baseDelay + 0.5}s` }}
              >
                {slide.rightContent[0].title}
              </h3>
              <p
                className={`text-gray-600 text-sm mb-4 ${getRightContentAnimationClass(
                  isAnimatingOut
                )}`}
                style={{ animationDelay: `${baseDelay + 0.6}s` }}
              >
                {slide.rightContent[0].description}
              </p>
              <button
                className={`px-5 py-2 bg-teal-500 text-white text-sm font-semibold rounded ${getRightContentAnimationClass(
                  isAnimatingOut
                )}`}
                style={{ animationDelay: `${baseDelay + 0.7}s` }}
              >
                {slide.rightContent[0].buttonText}
              </button>
            </div>
          </div>
          <div className="grid grid-cols-2 gap-4">
            <div
              className={`bg-white p-6 rounded-lg shadow-2xl ${getRightContentAnimationClass(
                isAnimatingOut
              )}`}
              style={{ animationDelay: `${baseDelay + 0.5}s` }}
            >
              <h3
                className={`font-bold text-xl mb-2 ${getRightContentAnimationClass(
                  isAnimatingOut
                )}`}
                style={{ animationDelay: `${baseDelay + 0.6}s` }}
              >
                {slide.rightContent[1].title}
              </h3>
              <p
                className={`text-gray-600 text-sm mb-4 ${getRightContentAnimationClass(
                  isAnimatingOut
                )}`}
                style={{ animationDelay: `${baseDelay + 0.7}s` }}
              >
                {slide.rightContent[1].description}
              </p>
              <button
                className={`px-5 py-2 bg-teal-500 text-white text-sm font-semibold rounded ${getRightContentAnimationClass(
                  isAnimatingOut
                )}`}
                style={{ animationDelay: `${baseDelay + 0.8}s` }}
              >
                {slide.rightContent[1].buttonText}
              </button>
            </div>
            <div
              className={`bg-teal-500/30 p-6 rounded-lg shadow-2xl flex items-center justify-center cursor-pointer group ${getRightContentAnimationClass(
                isAnimatingOut
              )}`}
              style={{ animationDelay: `${baseDelay + 0.7}s` }}
            >
              <div className="w-16 h-16 rounded-full bg-white/30 group-hover:bg-white/50 transition-colors flex items-center justify-center">
                <div className="w-0 h-0 border-t-8 border-t-transparent border-l-12 border-l-white border-b-8 border-b-transparent ml-1"></div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Carousel;
